import React from 'react';
import ErrorLayout, { ErrorLayoutProps } from '../ErrorLayout';

export interface ServerErrorProps extends Partial<ErrorLayoutProps> {
  message?: string;
  customImage?: string;
  contactInfo?: string;
  isRetryable?: boolean;
  onRetry?: () => void;
}

const ServerError: React.FC<ServerErrorProps> = ({
  message = "We're sorry, but something went wrong on our server.",
  customImage,
  contactInfo = "If this problem persists, please contact our support team.",
  isRetryable = true,
  onRetry,
  ...errorLayoutProps
}) => {
  // Default illustration for 500 error
  const defaultIllustration = 'data:image/svg+xml;base64,';

  // Handle retry button click
  const handleRetry = () => {
    if (onRetry) {
      onRetry();
    } else {
      window.location.reload();
    }
  };

  return (
    <ErrorLayout
      title="Server Error"
      subtitle={message}
      errorCode={500}
      illustration={customImage || defaultIllustration}
      primaryColor="#ef4444" // Red color for server errors
      {...errorLayoutProps}
    >
      <div style={{ marginBottom: '2rem' }}>
        <p style={{ marginBottom: '1rem' }}>{contactInfo}</p>
        
        {isRetryable && (
          <button
            onClick={handleRetry}
            style={{
              backgroundColor: '#ef4444',
              border: 'none',
              color: 'white',
              padding: '0.75rem 1.5rem',
              borderRadius: '0.375rem',
              fontWeight: 500,
              cursor: 'pointer',
              transition: 'all 0.2s',
              marginTop: '1rem',
            }}
          >
            Try again
          </button>
        )}
      </div>
    </ErrorLayout>
  );
};

export default ServerError;
